html {
  line-height: 1.15;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "RocknRoll One", sans-serif;
}

.intro_container {
  width: 100%;
  min-height: 100vh;
  background-color: #494949;
  display: flex;
  align-items: center;
  justify-content: center;
}

.intro_info_container {
  text-align: center;
  color: #fff;
}

.title-container p {
  padding: 1.5rem;
  font-size: 150%;
  text-transform: uppercase;
  font-family: "Raleway", sans-serif;
  letter-spacing: 1;
  color: #fff;
}

.title-container {
  height: 50px;
}

.intro_info_container h1 {
  font-size: 2rem;
  text-align: center;
  font-weight: 600 !important;
  color: white;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  margin: 0 auto 18px auto;
  width: 100%;
}

.logo {
  width: 8.5rem;
  height: 6.5rem;
  position: absolute;
  top: 8px;
  left: 4px;
}
.logo img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

.styles_typicalWrapper__1_Uvh::after {
  cursor: none !important;
  display: none;
}

.section-title {
  padding: 2rem;
  font-size: 1.4rem;
  font-weight: 1;
  text-align: center;
  color: #fff;
}

.skills_section {
  width: 100%;
  height: 45vh;
  background-color: #1f1f1f;
}
/* ._container {
  text-align: center;
} */
.icon_container {
  width: 100%;
}

.text-white {
  color: #fff;
}

.list_icons_names {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.list-inline {
  background-color: #443f3b;
  border-radius: 10px;
  width: 6rem;
  margin: 0.6rem;
}

.inline_container {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.about {
  width: 100%;
  min-height: 70vh;
  background-color: rgb(199, 138, 59);
}

.about_container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

.about_info {
  width: 24rem;
  height: auto;
  margin: 1rem 5px;
  background-color: white;
  font-size: 1rem;
  padding: 1rem;
  line-height: 150%;
  font-weight: normal;
  border: 1px solid black;
  border-radius: 4px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}
.avatar {
  display: block;
  width: 12rem;
  height: 23rem;
  margin: 1rem 0;
}

.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

